.baseLayout {
  height: 100%;
}
.baseContent {
  height: 100%;
  overflow: hidden;
  position: relative;
}
.baseContent > div {
  height: 100%;
  width: 100%;
  padding: 40px 20px;
}
.baseBread {
  position: absolute;
  top: 10px;
  left: 20px;
  z-index: 999;
  font-size: 14px;
}
.baseTips {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 14px;
  color: #858688;
}
